@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/alumnisans-semibold.woff2") format("woff2"), url("../fonts/alumnisans-semibold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/alumnisans-extrabold.woff2") format("woff2"), url("../fonts/alumnisans-extrabold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/robotocondensed-light.woff2") format("woff2"), url("../fonts/robotocondensed-light.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  min-width: 320px;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

[data-tabs=parent] {
  overflow: hidden;
}
[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}
[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.btn {
  display: inline-flex;
  color: #ffffff;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ed0233;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.tabs__element {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 4%;
  padding: 0;
  margin: 0;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .tabs__element {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 40px;
  }
}

.tabs__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 68px 18%;
  background: #ffffff;
  border: 7px solid #ffffff;
}
.tabs__item:hover {
  border: 10px solid;
  border-image-slice: 1;
  border-width: 7px;
  border-image-source: linear-gradient(148.82deg, #d10a42 0.85%, #00359f 100%);
}
@media (max-width: 1199px) {
  .tabs__item {
    width: 442px;
    padding: 68px 110px;
  }
}
@media (max-width: 767px) {
  .tabs__item {
    width: 100%;
    padding: 55px 42px;
  }
}
.tabs__item h3 {
  font-size: 26px;
  line-height: 29px;
  margin: 0 0 22px;
}
@media (max-width: 767px) {
  .tabs__item h3 {
    margin: 0 0 20px;
    font-size: 22px;
    line-height: 24px;
  }
}
.tabs__item p {
  margin: 0 0 96px;
}
@media (max-width: 767px) {
  .tabs__item p {
    margin: 0 0 81px;
  }
}

.tabs__link {
  padding: 20px 10px;
  width: 100%;
  display: flex;
  justify-content: center;
  border: 2px solid #102654;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  margin-top: auto;
}

.tabs__box {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  font-weight: 700;
  font-size: 60px;
  line-height: 90px;
  color: #103AAE;
  column-gap: 14px;
  margin: 0 0 59px;
}
@media (max-width: 767px) {
  .tabs__box {
    font-size: 48px;
    line-height: 52px;
  }
}
.tabs__box span {
  position: relative;
  z-index: 2;
}
.tabs__box span:before {
  position: absolute;
  top: -54px;
  right: -66px;
  font-weight: 700;
  font-size: 100px;
  line-height: 150px;
  color: #E8E8E8;
  z-index: -1;
  content: attr(data-price);
}
@media (max-width: 767px) {
  .tabs__box span:before {
    font-size: 80px;
    line-height: 120px;
  }
}
.tabs__box img {
  z-index: 2;
}
@media (max-width: 767px) {
  .tabs__box img {
    width: 24px;
    height: 33px;
  }
}

.tabs__controls {
  width: max-content;
  justify-content: center;
  max-width: 100%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 46px;
  color: #102654;
  margin-bottom: 39px;
  position: relative;
}
.tabs__controls::after {
  position: absolute;
  bottom: 0;
  content: "";
  width: 100%;
  height: 2px;
  background: rgba(120, 120, 120, 0.2);
}
@media (max-width: 767px) {
  .tabs__controls {
    gap: 54px;
  }
}
@media (max-width: 767px) {
  .tabs__controls {
    gap: 45px;
  }
}

.tabs__control {
  color: #102654;
  padding: 10px 0;
  margin-bottom: 10px;
  position: relative;
  font-size: 20px;
  line-height: 30px;
  color: rgba(16, 38, 84, 0.5);
}
.tabs__control.is-active {
  color: #102654;
}
.tabs__control.is-active::after {
  position: absolute;
  bottom: -10px;
  content: "";
  width: 100%;
  height: 2px;
  background: #d10a42;
}
@media (max-width: 1199px) {
  .tabs__control {
    font-size: 18px;
    line-height: 27px;
  }
}
@media (max-width: 767px) {
  .tabs__control {
    font-size: 16px;
    line-height: 24px;
  }
}

.tabs__item:hover > .tabs__link,
.tabs__link:focus-visible {
  color: #ffffff;
  background: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
  border: none;
  outline: none;
}

.header {
