  margin: 0 0 25px;
}
.reviews p {
  font-size: 16px;
  line-height: 22px;
  margin: 0;
}

.reviews .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
@media (max-width: 767px) {
  .reviews .container {
    padding: 0 47px;
  }
}

.reviews__list {
  color: #ffffff;
  display: flex;
  padding: 0;
  cursor: grab;
}

.reviews__swiper {
  min-height: none;
  overflow: hidden;
  width: 560px;
  display: block;
  margin: 0;
  padding: 0;
}
@media (max-width: 1199px) {
  .reviews__swiper {
    width: 566px;
  }
}
@media (max-width: 767px) {
  .reviews__swiper {
    max-width: 100%;
  }
}

.reviews__list {
  list-style-type: none;
}

.reviews__content {
  background: #1c3374;
  padding: 30px 40px;
  width: 560px;
  display: grid;
  grid-template-columns: 80px 1fr;
  column-gap: 28px;
}
.reviews__content img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.reviews__content p {
  grid-column: 2/3;
}
@media (max-width: 767px) {
  .reviews__content p {
    grid-column: 1/3;
  }
}
@media (max-width: 767px) {
  .reviews__content {
    grid-template-columns: 68px 1fr;
    max-width: 100%;
  }
}

.reviews__content-image {
  width: 80px;
  height: 80px;
  display: block;
  clip-path: circle(50% at 50% 50%);
}

.reviews__btn {
  background: transparent;
  border: none;
  position: absolute;
  top: 50%;
}
@media (max-width: 767px) {
  .reviews__btn {
    width: 10px;
    height: 19px;
  }
}
.reviews__btn--right {
  right: 50%;
  transform: rotate(180deg) translate(-350px, -20px);
}
@media (max-width: 767px) {
  .reviews__btn--right {
    right: 0;
    transform: rotate(180deg) translate(10px, -35px);
  }
}
.reviews__btn--left {
  transform: translate(-350px, 30px);
  left: 50%;
}
@media (max-width: 767px) {
  .reviews__btn--left {
    left: 0;
    transform: translate(10px, 30px);
  }
}

.swiper-slide {
  height: min-content;
}

.contacts {
  overflow: hidden;
}

.contacts .container {
  display: flex;
}

.contacts form {
  padding: 56px 170px 70px 0;
  background: #102654;
  position: relative;
}
.contacts form::after {
  content: "";
  z-index: -1;
  top: 0;
  right: 0;
  position: absolute;
  width: 100vw;
  height: 100%;
  background: #102654;
}

.contacts fieldset {
  display: flex;
}

.contacts__address {
  padding: 56px 0 70px 120px;
}

.footer {
  padding: 44px 0;
  background: #030a1a;
}

.footer .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

